import { CallToAction } from "../atoms/call-to-action";
import { Link } from "gatsby";
import React from "react";
import styled from "styled-components";

const InternalLink = styled(Link)`
  color: ${(props) => props.theme.light.primaryColorText};
  text-decoration: none;

  &:hover {
    color: ${(props) => props.theme.light.primaryColorText};
    text-decoration: none;

    @media (prefers-color-scheme: dark) {
      color: ${(props) => props.theme.dark.primaryColorText};
    }
  }

  @media (prefers-color-scheme: dark) {
    color: ${(props) => props.theme.dark.primaryColorText};
  }
`;

interface CallToActionInternalProps {
  to: string;
  onClick: () => void;
}

export const CallToActionInternal: React.FC<CallToActionInternalProps> = ({
  children,
  to,
  onClick,
}) => (
  <CallToAction>
    <InternalLink to={to} onClick={onClick}>
      {children}
    </InternalLink>
  </CallToAction>
);
